import React from 'react';
import banner from './Images/Banner.png'
import model1 from './Images/Model1.jpg'
import model2 from './Images/Model2.jpg'
import model3 from './Images/Model3.jpg'

function About() {
	return (
		// <!-- about section starts -->
		<section className="about section-padding " id="about">
			<div className="banner-image">
				<img alt="" className="img-fluid" src={banner} />
			</div>
			<div className="container">
				<div className="row">
					<div className="mt-md-5">
						<div className="about-text" style={{ alignItems: 'center' }} >
							<h2 style={{ color: 'white', fontFamily: 'Aladin', textAlign: 'center' }} >Our Agency</h2>
						</div>
						<div  className="mt-md-5" style={{textAlign: 'center' }} >
							<img alt="" class="img-fluid rounded-circle img-sides" src={model2} />
							<img alt="" class="img-fluid rounded-circle img-main" src={model1} />
							<img alt="" class="img-fluid rounded-circle img-sides" src={model3} />
						</div>
					</div>
				</div>
			</div>




		</section>
	);
}

export default About;
