import React from 'react';
import { Button, Carousel, Col, Container, Row } from 'reactstrap';
import logo1 from './Images/Slider.png'
import logo2 from './Images/Slider2.png'

export default function HomeSlider() {
  return (
    <div id='home'>


      <div className="carousel slide " data-bs-ride="carousel" id="carouselExampleIndicators">
        <div className="carousel-indicators">
          <button aria-label="Slide 1" className="active" data-bs-slide-to="0" data-bs-target="#carouselExampleIndicators" type="button"></button> 
          <button aria-label="Slide 2" data-bs-slide-to="1" data-bs-target="#carouselExampleIndicators" type="button"></button> 
        </div>
        <div className="carousel-inner  ">
          <div className="carousel-item active">
            <img alt="..." className="d-block w-100 h-100" src={logo1}/>
              <div className="carousel-caption">
                <h5>World's Best Leading Model Agency</h5>
                <p>Fashion is an instant language, it's time you learn it. Look for the magic in every moment.</p>
                <p><a type="button" className="btn btn-danger btn-square-md" href='#about' >Discover</a> <a type="button" class="btn btn-outline-light btn-square-md">Join Us</a></p>
              </div>
          </div>
          <div className="carousel-item ">
            <img alt="..." className="d-block w-100 h-100" src={logo2}/>
              <div className="carousel-caption">
                <h5>Do You want to be a Model?</h5>
                <p>Fashion is an instant language, it's time you learn it. Look for the magic in every moment. 
                  Life is so short that there is no reason not to go for what sets your soul on fire.
                  Modeling is not about flaunting your body, but getting comfortable with it.</p>
                  <p><a type="button" className="btn btn-danger btn-square-md" href='#footer' >Contact Us</a></p>
              </div>
          </div>
        </div>
        <button className="carousel-control-prev" data-bs-slide="prev" data-bs-target="#carouselExampleIndicators" type="button"><span aria-hidden="true" className="carousel-control-prev-icon"></span> <span className="visually-hidden">Previous</span></button> <button className="carousel-control-next" data-bs-slide="next" data-bs-target="#carouselExampleIndicators" type="button"><span aria-hidden="true" className="carousel-control-next-icon"></span> <span className="visually-hidden">Next</span></button>
      </div>

    </div>
  );
}

